.TodoSearch {
  background: #8fccff;
  border-radius: 2px;
  border: 2px solid #202329;
  margin: 0 24px;
  height: 64px;
  width: calc(100% - 62px);
  font-size: 24px;
  text-align: center;
  font-family: 'Montserrat';
  font-weight: 400;
  color: #1E1E1F;
  border-radius: 16px;
  box-shadow: 5px 5px 5px rgba(32, 35, 41, 0.25);
}

.TodoSearch::placeholder {
  color: #000000;
  font-family: 'Montserrat';
  font-weight: 400;
}

.TodoSearch:focus {
  outline-color: #61DAFA;
}
